
 .githubContribution {
  color: #ffff;
  font-weight: 800;
  font-family: Arial, Helvetica, sans-serif;
  position: relative;
  text-transform: uppercase;
  font-size: 2.6em;
  letter-spacing: 4px;
  overflow: hidden;
  background: linear-gradient(90deg, rgb(164, 133, 255), #fff, #000);
  background-repeat: no-repeat;
  background-size: 80%;
  animation: animate 3s linear infinite;
  -webkit-background-clip: text;
  -webkit-text-fill-color: rgba(255, 255, 255, 0);
  background-color: orange;

  background-image: radial-gradient(
      at 40% 20%,
      hsla(82, 100%, 74%, 1) 0px,
      transparent 50%
    ),
    radial-gradient(at 80% 0%, hsla(243, 100%, 56%, 1) 0px, transparent 50%),
    radial-gradient(at 0% 50%, hsla(49, 100%, 93%, 1) 0px, transparent 50%),
    radial-gradient(at 80% 50%, hsla(34, 100%, 76%, 1) 0px, transparent 50%),
    radial-gradient(at 0% 100%, hsla(76, 100%, 77%, 1) 0px, transparent 50%),
    radial-gradient(at 80% 100%, hsla(296, 100%, 70%, 1) 0px, transparent 50%),
    radial-gradient(at 0% 0%, hsla(37, 100%, 76%, 1) 0px, transparent 50%);
}

.gitImg{
  border-radius: 5px;
}



























.gitStats {
  /* border: 3px solid #fff; */
  position: relative;
}

.headingContainer {
  /* border: 1px solid #fff; */
  display: flex;
  width: 35%;
  margin: auto;
  padding: 30px;
}
.heading {
  font-size: 20px;
  text-align: center;
  color: #fff;
}
.orangeDash {
  border: 1px solid orange;
  margin: auto;
  width: 75px;
  background-color: orange;
}

.calender {
  border: 1px solid #fff;
  width: 65%;
  height: 200px;
  margin: auto;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 10px;
}

.commits {
  /* border: 1px solid yellow; */
  width: 68%;
  height: auto;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 10px;
  align-items: center;
  justify-content: center;
}
.commit1 {
  width: 40%;
}
.commit2 {
  width: 40%;
}
.commit3 {
  width: 35%;
}

/* Media Query for Mobile Devices */
@media (min-width: 320px) and (max-width: 767px) {
  .headingContainer {
    /* border: 1px solid yellow; */
    width: 94%;
    padding: 20px 0;
  }
  .heading {
    font-size: 19px;
  }
  .orangeDash {
    width: 45px;
  }
  .calendar {
    /* border: 1px solid #fff; */
    width: 95%;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 15px;
  }
  .commits {
    /* border: 1px solid yellow; */
    width: 89%;
    gap: 15px;
  }
  .commit1 {
    width: 100%;
  }
  .commit2 {
    width: 100%;
  }
  .commit3 {
    width: 100%;
  }
}

/* Media Query for Tablets Ipads portrait mode */
@media (min-width: 768px) and (max-width: 1024px) {
  .headingContainer {
    /* border: 1px solid yellow; */
    display: flex;
    width: 58%;
    margin: auto;
    padding: 20px;
  }
  .heading {
    font-size: 20px;
  }
  .orangeDash {
    width: 70px;
  }
  .calendar {
    /* border: 1px solid #fff; */
    width: 90%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 20px;
  }
  .commits {
    /* border: 1px solid yellow; */
    width: 90%;
    height: 350px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    gap: 10px;
    margin-top: 10px;
    padding: 0px 10px;
  }
  .commit1 {
    width: 48%;
  }
  .commit2 {
    width: 48%;
  }
  .commit3 {
    width: 40%;
  }
}

/* Media Query for Laptops and Desktops */
@media (min-width: 1025px) and (max-width: 1280px) {
  .headingContainer {
    /* border: 1px solid yellow; */
    display: flex;
    width: 38%;
    margin: auto;
    padding: 5px;
  }
  .heading {
    font-size: 20px;
  }
  .orangeDash {
    width: 75px;
  }
  .calendar {
    /* border: 1px solid #fff; */
    width: 75%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .commits {
    /* border: 1px solid yellow; */
    width: 88%;
    height: 440px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    gap: 10px;
    margin-top: 10px;
    padding: 0px 5px;
  }
  .commit1 {
    width: 48%;
  }
  .commit2 {
    width: 48%;
  }
  .commit3 {
    width: 40%;
  }
}